import React from 'react'
import injectSheet from 'react-jss'
import Carousel from '../carousel/Carousel'
import Slide from 'react-reveal/Slide';

const styles = {
  '@global body': {
    backgroundColor: '#CC5A71',
    paddingBottom: 20
  },
  container: {
    height: '100%',
    color: 'white',
    paddingTop: 50,
    paddingLeft: 200,
    paddingRight: 200,
    lineHeight: '1.5rem',
    '@media (max-width: 800px)': {
      paddingLeft: 40,
      paddingRight: 40,
    }
  },
  row: {
    display: 'flex',
    flexFlow: 'row nowrap',
    justifyContent: 'space-between',
    paddingTop: 10,
    paddingBottom: 10,
    flexBasis: '100%',
    '@media (max-width: 800px)': {
      flexFlow: 'column nowrap',
    }
  },
  rowImages: {
    width: '100%',
    height: 'auto',
    textAlign: 'center',
    boxShadow: '0 0 20px 0 rgba(0, 0, 0, 0.2), 0 0px 20px 0 rgba(0, 0, 0, 0.19)',
  },
  largeLogo: {
    paddingTop: 10,
    width: '50%',
    height: 'auto',
    marginRight: 20,
    '@media (max-width: 800px)': {
      width: '100%'
    }
  }
}

const Projects = ({ classes }) => (
  <div className={classes.container}>
    <h1>Projects</h1>
    <div className={classes.row}>
      <p>
        Clock IO
        <br />
        • React, Redux, Go, PostgreSQL
        <br /><br />
        Time tracking application developed with a friend. Used to manage time management issues
        when working with personal or professional projects. <a href='https://clockio.online'>Site</a>
      </p>
    </div>
    <div className={classes.rowImages}>
      <Carousel>
        <Slide right>
          <div>
            <img src='images/clock1.png' alt='' className={classes.largeLogo} />
          </div>
        </Slide>
        <Slide right>
          <div>
            <img src='images/clock2.png' alt='' className={classes.largeLogo} />
          </div>
        </Slide>
        <Slide right>
          <div>
            <img src='images/clock3.png' alt='' className={classes.largeLogo} />
          </div>
        </Slide>
      </Carousel>
    </div>
  </div>
)

export default injectSheet(styles)(Projects)


